<template>
  <div>
    <div style="display: flex;justify-content: center">
      <div class="todo-list">
        <md-card>
          <md-card-header :data-background-color="dataBackgroundColor">
            <h4 class="title circles-title">Users</h4>
          </md-card-header>
          <md-card-content>
            <User v-for="user in showUsers" :key="user.file_path" :user="user"/>
          </md-card-content>
        </md-card>
      </div>
    </div>
  </div>
</template>

<script>
import User from "./User";

export default {
  name: 'users',
  components: {User},
  props: {
    dataBackgroundColor: {
      type: String,
      default: "green"
    }
  },
  computed: {
    showUsers() {
      return this.$store.getters.retrieveUsers
    }
  },
  methods: {
    fetchUsers() {
      this.$store.dispatch('retrieveUsers')
    }
  },
  created() {
    this.fetchUsers()
  }
}
</script>
<style>
.todo-list {
  width: 75%;
  display: flex;
  justify-content: center;
}
</style>